.slider {
  width: var(--length, 100%);
  height: var(--size, 10px);
  padding: 4px;
}

.slider input[type="range"] {
  -webkit-appearance: none;
  background: var(--acent-background);
  padding: 4px 3px;
  border-radius: 5px;
  width: var(--length, 100%);
  height: var(--size, 10px);
}

.slider input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: var(--size, 10px);
  border-radius: 10px;
  background-color: var(--acent-color);
}

.slider.vertical {
  position: relative;
  width: 100%;
  height: 100%;
}

.slider.vertical input[type="range"] {
  width: var(--length, 100%);
  height: var(--size, 10px);
  transform: rotate(-90deg) translateY(calc(var(--size, 10px) / 2));
  transform-origin: left;
  position: absolute;
  left: 0;
  bottom: 0;
}
