.SongPlayer {
  width: 500px;
  margin: 0 auto;
  padding: 10px 10px 20px;
  background-color: var(--primary-color);
  color: var(--acent-color);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SongPlayer img {
  margin: 10px auto 20px;
  border-radius: 5px;
  width: 250px;
  height: 250px;
}

.SongPlayer button {
  font-size: 32px;
  padding: 5px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  font-weight: bold;
  background-color: var(--acent-background);
  color: var(--acent-color);
  border: none;
  cursor: pointer;
}

.SongPlayer .progress {
  --length: 70%;
}
.SongPlayer .volume {
  width: 30px;
  height: 70px;
  padding-left: 10px;
}

.SongPlayer .volume .slider {
  --length: 60px;
  flex-basis: 20px;
}

.SongPlayer nav {
  display: flex;
  width: 70%;
  margin: 10px auto 0 auto;
  justify-content: space-around;
}
