:root {
  --primary-color: #244086;
  --acent-background: #171717;
  --acent-color: white;
}
body {
  background: var(--acent-background);
  color: var(--acent-color);
  margin: 0;
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
}

h1 {
  margin: 10px;
}
